<template>
  <div
    @click="mostrar = !mostrar"
    class="
      w-6
      h-6
      hijo
      flex-none flex
      items-center
      justify-center
      text-xs
      font-bold
      cursor-pointer
      relative
      text-black
    "
    :class="{
      cero: color === 0,
      uno: color === 1,
      dos: color === 2,
      tres: color === 3,
    }"
    :style="{ 'grid-column-start': semana, 'grid-column-end': semana }"
  >
    {{ valor }}
    <div
      v-if="mostrar && encabezado !== 'si'"
      class="
        absolute
        -top-full
        p-2
        bg-gray-300
        z-50
        w-72
        text-xs
        rounded-xl
        flex flex-col
        items-center
        justify-start
      "
    >
      <button class="bg-red-100 text-red-400 mb-2 w-full py-2 rounded-lg">
        Cerrar
      </button>
      <div class="w-full text-black">
        <div
          @click="mostrarOT = !mostrarOT"
          v-for="(item, index) in arrayActividades"
          :key="index"
          class="actividad"
        >
          <h1>{{ item.fecha }}</h1>
          <h1 class="mx-2">-</h1>
          <h1>{{ item.creadoPor }}</h1>
        </div>
      </div>
    </div>

    <!-- SHOW OT -->
    <teleport to="#modales" v-if="mostrarOT">
      <div
        class="
          absolute
          top-0
          pt-24
          flex flex-col
          items-center
          justify-start
          z-50
          overflow-y-auto
          w-full
          h-screen
          bg-black
        "
      >
        <div
          class="
            absolute
            top-0
            py-2
            px-4
            rounded-b-3xl
            bg-gray-300
            opacity-60
            cursor-pointer
          "
          @click="mostrarOT = !mostrarOT"
        >
          <h1>Clic para cerrar</h1>
        </div>
        <div class="w-full md:w-1/4 flex flex-col items-center justify-start">
          <div
            class="
              px-4
              pb-4
              w-full
              flex flex-col
              items-start
              justify-start
              text-left
            "
          >
            <div class="mb-4 text-white">
              <h1 class="font-bold uppercase">
                Llegada de Habitaciones Bávaro
              </h1>
              <h1 class="font-bold uppercase">
                Por: <span class="italic">Eduardo Meneses</span>
              </h1>
              <h1 class="font-bold uppercase">
                El: <span class="italic">14-10-2021</span>
              </h1>
            </div>
            <!-- Listado -->
            <div
              class="
                flex flex-col
                items-center
                justify-start
                w-full
                shadow
                rounded-xl
              "
            >
              <div
                class="
                  flex flex-col
                  items-center
                  justify-start
                  w-full
                  shadow
                  rounded-xl
                "
              >
                <SemanaApartadoSabanas
                  apartado="JAcuzzy"
                ></SemanaApartadoSabanas>
                <SemanasActividadSabanaLlena
                  tienefoto="no"
                  tienecomentaio="no"
                  comentario="Aqui va el comentario que haga el tecnico sobre la actividad solicitada"
                  tipo="1"
                  fotoadjunta="https://picsum.photos/200"
                  v-for="(item, index) in 5"
                  :key="index"
                  actividad="Realizar x cosa en el nose que cosa del no se que en el asasasl"
                ></SemanasActividadSabanaLlena>
                <SemanaApartadoSabanas
                  apartado="Luminarias"
                ></SemanaApartadoSabanas>
                <SemanasActividadSabanaLlena
                  tienefoto="si"
                  tienecomentaio="si"
                  comentario="Aqui va el comentario que haga el tecnico sobre la actividad solicitada"
                  tipo="2"
                  fotoadjunta="https://picsum.photos/200"
                  v-for="(item, index) in 5"
                  :key="index"
                  actividad="Realizar x cosa en el nose que cosa del no se que en el asasasl"
                ></SemanasActividadSabanaLlena>
                <SemanaApartadoSabanas apartado="Agua"></SemanaApartadoSabanas>
                <SemanasActividadSabanaLlena
                  tienefoto="si"
                  tienecomentaio="si"
                  comentario="Aqui va el comentario que haga el tecnico sobre la actividad solicitada"
                  tipo="3"
                  fotoadjunta="https://picsum.photos/200"
                  v-for="(item, index) in 6"
                  :key="index"
                  actividad="Realizar x cosa en el nose que cosa del no se que en el asasasl"
                ></SemanasActividadSabanaLlena>
              </div>
            </div>
            <!-- Listado -->
          </div>
        </div>
      </div>
    </teleport>
    <!-- SHOW OT -->
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import SemanasActividadSabanaLlena from "./SemanasActividadSabanaLlena.vue";
import SemanaApartadoSabanas from "./SemanaApartadoSabanas.vue";
export default {
  components: { SemanasActividadSabanaLlena, SemanaApartadoSabanas },
  props: {
    array: {
      type: Object,
      valor: [],
    },

    semana: {
      type: Number,
      required: true,
    },

    cantidad: {
      type: Number,
      required: false,
    },

    encabezado: {
      type: String,
      required: false,
    },
  },

  setup(props) {
    const valor = ref(props.cantidad > 0 ? props.cantidad : "");
    const mostrar = ref(false);
    const mostrarOT = ref(false);
    const color = ref(0);

    const arrayActividades = ref([]);

    if (props.array.registros) {
      props.array.registros.forEach((element, index) => {
        if (element.semana == props.semana) {
          arrayActividades.value.push(element);
        }
      });
    }

    if (arrayActividades.value.length) {
      color.value = 1;
    }

    return { valor, mostrar, mostrarOT, arrayActividades, color };
  },
};
</script>

<style scoped>
.hijo {
  grid-row-start: 1;

  border-width: 0.16rem;

  border-radius: 4px;
  margin-right: 2px;
}
.cero {
  border-color: #414141;
  color: #414141;
  background-color: rgb(255, 255, 255);
}
.uno {
  border-color: #80c722;
  color: #80c722;
  background-color: rgb(230, 255, 215);
}
.dos {
  border-color: #ff9d42;
  color: #ff9d42;
  background-color: rgb(255, 234, 215);
}

.tres {
  border-width: 0;
}
.tres:hover {
  background-color: whitesmoke;
}

.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}
.actividad:hover {
  @apply bg-blue-100;
}
.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}
.actividad:first-child {
  @apply rounded-t-xl;
}
</style>
