<template>
  <div
    class="w-full bg-gris-30 h-screen flex flex-col items-center justify-start"
  >
    <div class="w-full p-4 font-bold text-xl">
      <h1>Historial de Sábanas</h1>
    </div>
    <div
      class="
        w-full
        md:p-4
        p-1
        font-bold
        text-xl
        flex flex-row flex-wrap
        items-center
        justify-center
      "
    >
      <!-- FECHA INICIAL -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Fecha Inicial</h1>
        <input
          type="date"
          class="p-3 rounded shadow focus:outline-none w-full md:w-auto"
          v-model="filtros.fechaInicial"
        />
      </div>

      <!-- FECHA FINAL -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Fecha Final</h1>
        <input
          type="date"
          class="p-3 rounded shadow focus:outline-none w-full md:w-auto"
          v-model="filtros.fechaFinal"
        />
      </div>

      <!-- HOTELES -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Hotel</h1>
        <select
          class="p-3 rounded shadow focus:outline-none w-full md:w-auto"
          v-model="filtros.idHotel"
          @change="
            obtenerRegistros();
            obtenerSabanas(filtros.idHotel);
          "
        >
          <option value="">Seleccione Hotel</option>
          <option
            v-for="(item, index) in arrayHoteles"
            :key="index"
            :value="item.idHotel"
          >
            {{ item.hotel }}
          </option>
        </select>
      </div>

      <!-- SABANAS -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Sábana</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto">
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arraySabanas"
            :key="index"
            :value="item.idSabana"
          >
            {{ item.sabana }}
          </option>
        </select>
      </div>

      <!-- VILLAS -->
      <div class="text-sm md:mr-4 md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Villa</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto">
          <option value="1">Todo</option>
          <option value="1">Villa 55</option>
          <option value="1">Villa 55</option>
          <option value="1">Villa 55</option>
          <option value="1">Villa 55</option>
          <option value="1">Villa 55</option>
        </select>
      </div>

      <!-- VISTAS -->
      <div class="text-sm md:mb-0 mb-4 md:w-auto w-1/2 px-4">
        <h1 class="uppercase">Visualizar</h1>
        <select class="p-3 rounded shadow focus:outline-none w-full md:w-auto">
          <option value="1">Todo</option>
          <option value="1">Solo habitaciones con sabanas</option>
        </select>
      </div>
    </div>

    <div
      class="
        flex flex-col
        items-start
        justify-start
        w-full
        md:p-4
        pl-0
        py-4
        pr-2
        overflow-x-auto
      "
      v-for="(item, index) in array"
      :key="index"
    >
      <!-- HOTELES -->
      <div
        class="
          flex flex-row
          items-center
          justify-center
          bg-gray-600
          p-2
          font-bold
          mb-2
          text-white
        "
      >
        <h1>{{ item.hotel }}</h1>
      </div>
      <div
        class="
          flex flex-row
          items-center
          justify-center
          hover:bg-gray-200
          py-1
          sticky
          md:relative
          -top-6
          md:top-0
          z-20
          bg-gris-30
        "
      >
        <div class="w-56 mr-2">
          <h1 class="font-bold"></h1>
        </div>

        <div class="contenedor_cuadritos">
          <SemanasCuadrito
            v-for="(item, index) in 52"
            :key="index"
            :semana="index + 1"
            :cantidad="index + 1"
            :color="3"
            encabezado="si"
            :array="[]"
          />
        </div>
      </div>

      <!-- CONTENEDOR DE EQUIPO CON LAS 52 SEMANAS -->
      <div
        v-for="(equipos, index) in item.equipos"
        :key="index"
        class="flex flex-row items-center justify-center hover:bg-gray-200"
      >
        <!-- HABITACIÓN -->
        <div
          class="
            w-56
            mr-2
            sticky
            md:relative
            left-0
            z-30
            md:bg-transparent
            bg-gris-30
            py-2
          "
        >
          <h1 class="font-bold">{{ equipos.equipo }}</h1>
        </div>
        <!-- 52 SEMANAS -->
        <div class="contenedor_cuadritos">
          <SemanasCuadrito
            v-for="(item, index) in 52"
            :key="index"
            :semana="index + 1"
            :cantidad="index + 1"
            :color="0"
            encabezado="no"
            :array="equipos"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "@vue/runtime-core";
import { useStore } from "vuex";
import SemanasCuadrito from "@/components/SemanasCuadrito.vue";

export default {
  components: { SemanasCuadrito },
  props: {},
  setup() {
    const store = useStore();
    const sweetalert2 = require("sweetalert2");

    const fechaHoy = new Date();

    const filtros = ref({
      fechaInicial:
        fechaHoy.getFullYear() +
        "-" +
        (fechaHoy.getMonth() + 1 > 9
          ? fechaHoy.getMonth()
          : "0" + (fechaHoy.getMonth() + 1)) +
        "-" +
        fechaHoy.getDate(),
      fechaFinal:
        fechaHoy.getFullYear() +
        "-" +
        (fechaHoy.getMonth() + 1 > 9
          ? fechaHoy.getMonth()
          : "0" + (fechaHoy.getMonth() + 1)) +
        "-" +
        fechaHoy.getDate(),
      idHotel: "",
      idSabana: 0,
      idVilla: "",
      visualizar: 0,
    });

    onMounted(async () => {
      await obtenerRegistros();
    });

    const obtenerRegistros = async () => {
      await store.dispatch("getArray", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "semanas",
        fechaInicial: filtros.value.fechaInicial,
        fechaFinal: filtros.value.fechaFinal,
        idSabana: filtros.value.idHotel,
        idHotel: filtros.value.idHotel,
        idVilla: 0,
        visualizar: 0,
      });
    };

    const array = computed(() => {
      return store.state.array;
    });

    const arrayHoteles = computed(() => {
      return store.state.arrayHoteles;
    });

    const arraySabanas = computed(() => {
      return store.state.arraySabanas;
    });

    const obtenerSabanas = async (idHotel) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "obtenerSabanas",
        idHotel: idHotel,
      });

      if (response.response) store.state.arraySabanas = response.data.data;
    };

    return {
      obtenerRegistros,
      array,
      arrayHoteles,
      obtenerSabanas,
      arraySabanas,
      filtros,
    };
  },
};
</script>

<style scoped>
.contenedor_cuadritos {
  display: grid;
  grid-template-columns: repeat(52, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
</style>
